// CertPlatzi Component

import React from 'react'
import './cert-platzi.scss'
import {Link} from 'react-router-dom'

function CertPlatzi (props) {
  return (
    <div className="certPlatzi">
      <Link to={`/training/${props.url}`}>
        <img src={props.img} alt={props.school} loading="lazy" />
      </Link>

      <p className="">{props.name}</p>

    </div>
  )
}

export default CertPlatzi
