// CertPlatzi Component

import React from 'react'
import './certs-platzi.scss'

import platziImg from '../../../assets/images/techs/platzi.png'
import { getCerts, coursesPlatzi, getCert } from '../../../services/certs'
import CertPlatzi from './CertPlatzi'


function CertsPlatzi() {
  const careers = getCerts('type', 'career');
  return(
    <div className="certsPlatzi">
      <figure className="certsPlatzi-img">
        <a href="https://platzi.com/@AlbertoDSosa/" target="_blank" rel="noopener noreferrer">
          <img src={platziImg} alt="Platzi" loading="lazy" />
        </a>

      </figure>
      <h2>Certificados Platzi</h2>
      {
        careers.map((career) => {
          let img = require(`../../../assets/images/techs/${career.img}`)
          return <CertPlatzi
                  key={career.id}
                  img={img.default}
                  name={career.name}
                  url={career.uri}
                 />
        })
      }
      {
        coursesPlatzi.map(id => {
          const course = getCert('id', id)
          let image = require(`../../../assets/images/techs/${course.img || 'school.png'}`)
          
          return <CertPlatzi
                  key={course.id}
                  img={image.default}
                  name={course.name}
                  url={course.uri}
                 />
        })
      }
    </div>

  )
}

export default CertsPlatzi
